import React ,{Component} from 'react';
import { Select ,Form,Table,Button, Icon, DatePicker, LocaleProvider, } from 'antd';
import axios from 'axios'
import '../../../style/customerlist/timeorder.scss'
import zhCN from 'antd/lib/locale-provider/zh_CN';
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;

class timeorderForm extends Component{
      constructor(props){
        super(props)
        this.state={
            datanum:[],
             
        }
      }
      componentDidMount(){
        const  loginid=localStorage.getItem('loginid');
        const user=localStorage.getItem('name');
            //项目列表
            if(loginid!='' && user!=""){
              
            axios.post('/index.php/user/index',{
              num:loginid,
              name:user,
              y:1,
              n:10,
              type:'私客'
            })
            .then((res)=>{
              // console.log(res);
              if(parseInt(res.status) === 200){
                  console.log(res);
                 var number=res.data.data;
                 var arr=[];
                 if(number.length>0){
                 number.map((v,j)=>{
                 arr.push({
                         key:v.id,
                         tag:v.grade,
                         label:v.label,
                         re:v.re,
                         dai:v.dai,
                         fu:v.fu,
    
                         customername:v.name,
                         jkproject:v.project,
                         jkorigin:v.port,
                         rtime: v.time,
                          zjtime:v.g_time,
                         isnow:v.isnow,
    
                         txdate:v.grade,
                         rpeople:v.s_id,
                         wtpeople:v.sid,
                         tel:v.tel
                     })
                     this.setState({
                      datanum:arr,
                      loading:false,
                      total:res.data.total,
                     })
                 })
                }else{
                  this.setState({
                    datanum:'',
                    loading:false
                   })
                }
              }else{
                 console.log("请求数据失败")
              }
            }).catch((error)=>{
              console.log(error)
            })
         
          }
       


      }
      changePage=(current,page )=>{
        const {sou,value}=this.state;
           console.log(current,page);
           const  loginid=localStorage.getItem('loginid');
           const user=localStorage.getItem('name');
          if(sou==true){
            if(loginid!='' && user!=""){
            axios.post('/index.php/user/sou',{
              value:value,
              num:loginid,
              name:user,
              n:page,
              y:current
           })
           .then((res)=>{
             console.log(res);
            
             if(res.data.code===200){
              console.log('提交成功');
              
               var num=res.data.data;
               if(num.length>0){
               var arr=[];
               num.map((v,k)=>{
                     arr.push({
                      key:v.id,
                      tag:v.grade,
                      label:v.label,
                      re:v.re,
                      dai:v.dai,
                      fu:v.fu,
                      customername:v.name,
                      jkproject:v.project,
                      jkorigin:v.port,
                      rtime: v.time,
                       zjtime:v.g_time,
                      isnow:v.isnow,
  
                      txdate:v.grade,
                      rpeople:v.s_id,
                      wtpeople:v.sid,
                      tel:v.tel
                     })
               })
               this.setState({
                datanum:arr,
                loading:false,
                sou:true
               })
              }
              else{
                this.setState({
                  datanum:'',
                  loading:false
                 })
              }
             }
           }).catch((error=>{
              console.log(error);
           }))
          }
  
  
  
          }else{

              if(loginid!='' && user!=""){
            const  loginid=localStorage.getItem('loginid');
            const user=localStorage.getItem('name');
            axios.post('/index.php/user/g',{
              num:loginid,
              name:user,
              y:current,
              n:page
          })
          .then((res)=>{
             console.log(res)
             if(res.data.code==200){
              console.log('成功')
              var number=res.data.data;
              var array=[];
              if(number.length>0){
  
             
              number.map((v,j)=>{
                array.push({
                      key:v.id,
                      tag:v.grade,
                      label:v.lebel,
                      re:v.re,
                      dai:v.dai,
                      fu:v.fu,
                      customername:v.name,
                      jkproject:v.project,
                      jkorigin:v.port,
                      rtime: v.time,
                       zjtime:v.g_time,
                      isnow:v.isnow,
  
                      txdate:v.grade,
                      rpeople:v.s_id,
                      wtpeople:v.sid,
                      tel:v.tel
                  })
                  this.setState({
                   datanum:array,
                   loading:false,
                   total:res.data.total
                  })
              })
            }else{
              this.setState({
                datanum:'',
                loading:false
               })
            }
  
  
  
             }
          }).catch((error)=>{
              console.log(error)
          })
           
          }
        }
    
    
    
  
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('Received values of form: ', values);
            const  loginid=localStorage.getItem('loginid');
            const user=localStorage.getItem('name');
            if(loginid!='' && user!=""){
            axios.post('/index.php/user/sou',{
              value:values,
              num:loginid,
              name:user,
           })
           .then((res)=>{
             console.log(res);
            
             if(res.data.code===200){
              console.log('提交成功');
              
               var num=res.data.data;
               if(num.length>0){
               var arr=[];
               num.map((v,k)=>{
                     arr.push({
                      key:v.id,
                      tag:v.grade,
                      label:v.label,
                      re:v.re,
                      dai:v.dai,
                      fu:v.fu,

                      customername:v.name,
                      jkproject:v.project,
                      jkorigin:v.port,
                      rtime: v.time,
                     zjtime:v.g_time,
                      isnow:v.isnow,

                      txdate:v.grade,
                      rpeople:v.s_id,
                      wtpeople:v.sid,
                      tel:v.tel
                     })
               })
               this.setState({
                datanum:arr,
                loading:false,
                sou:true,
                value:values,
                // total:res.data.total
               })
              }
              else{
                this.setState({
                  datanum:'',
                  loading:false
                 })
              }
             }
           }).catch((error=>{
              console.log(error);
           }))
          }

          }
        })
      }


      render(){
        const { getFieldDecorator } = this.props.form;
        
      const paginationProps = {
        showSizeChanger: true,
        showQuickJumper: { goButton: <Button type="primary">确定</Button> },
        showTotal: ((total,count) => {
          // console.log(count);
          // return '第' + count + '页' + '/' + total + ' 条数据';
           return `共 ${total} 条`;
        }),
        // showTotal: () => `共${totals}条`,
        // pageSize: this.state.pageSize,
        // current: page.pageNum,
         total: this.state.total,
        // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
         onChange: (current,page) => this.changePage(current,page),
      };
     
      const rowSelection = {
        type:'radio',
        onChange: (selectedRowKeys, selectedRows) => {
          localStorage.setItem('customerid',selectedRowKeys[0]);
          this.genDetail();
          this.daikanData();
          this.chengjiaoData();
          this.getfangpi();
        },
        getCheckboxProps: record => ({
          disabled: record.name === 'Disabled User', // Column configuration not to be checked
          name: record.name,
        }),
      };
        const columns = [
            {
              title: '',
              dataIndex: 'tag',
                render:(text,record) =>{
                var lei=record.tag
                 var str=""
                 if( lei =="A" || lei=="A类" ){
                    return(
                       <a className="iconfont icon-zhongdianbiaozhu " href="javascript:;" style={{color:'red'}}></a> 
                    )
                 }
              }
  
            },
            {
              title: '',
              dataIndex: 'tag1',
              render:(text,record) =>{
                var type=record.label;
                if(type=="一客多看"){
                  return(
                    <a className="iconfont icon-ai-connection" href="javascript:;" style={{color:'rgba(101,195,255,1)'}}></a> 
                  )
               }
              }
            },
            {
              title: '标签',
              dataIndex: 'tag2',
              render:(text,record) =>{
                var num=record.fu;
                if(num=="复看"){
                  return(
                    <a className="iconfont icon-renshu" href="javascript:;" style={{color:'rgba(196,132,255,1)'}}></a> 
                  )
               }
              }
            },
            {
              title: '',
              dataIndex: 'tag3',
                render:(text,record) =>{
                var dai=record.dai;
                if(dai=="新客" || dai=="老客" ){
                  return(
                    <a className="iconfont icon-wo" href="javascript:;" style={{color:'rgba(41,204,114,1)'}}></a> 
                  )
               }
              }
            },
            {
              title: '',
              dataIndex: 'tag4',
              render:(text,record) =>{
                var re=record.re;
              if(re=="成交"){
                return(
                  <a className="iconfont icon-chengjiao" href="javascript:;" style={{color:'rgba(255,191,52,1)'}}></a> 
                )
              }
            }
            },
            {
              title: '客户姓名',
              dataIndex: 'customername',
            },
            {
              title: '进客项目',
              dataIndex: 'jkproject',
            },
            {
              title: '进客来源',
              dataIndex: 'jkorigin',
            },
            {
              title: `登记时间`,
              dataIndex: 'rtime',
              defaultSortOrder: 'descend', //上升
              sorter: (a, b) => {
                // console.log(a.rtime )
                // console.log(b.rtime )
                return b.rtime < a.rtime ? 1 : -1 
                  },
            },
            {
              title: '最近跟进时间',
              dataIndex: 'zjtime',
              render:(text,record)=>{
                   var now=record.isnow;
                   var time=record.zjtime
                   if(now==0){
                      return(
                          <span >{time}</span>
                      )
                   }if(now==1){
                       return(
                        <span style={{color:'red'}}>{time}</span>
                       )
                   }
              },
              sorter: (a, b) => {
                console.log(a);
                return b.zjtime < a.zjtime ? 1 : -1 
                  },
            },
            {
              title: '客户等级',
              dataIndex: 'txdate',
            },
            {
              title: '登记人',
              dataIndex: 'rpeople',
            },
            {
              title: '委托人',
              dataIndex: 'wtpeople',
            },
            {
              title: '电话',
              dataIndex: 'tel',
            },
  
          ];
        return(
          <LocaleProvider locale={zhCN}>
          <div className="timeorder">
                <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
                        <Form.Item label="时间" >
                        {getFieldDecorator('time', {
                            rules: [{ required: false, message: '请选择省' }],
                        })(
                            <RangePicker  />
                        )}
                    </Form.Item>
                    
                    <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
                    查询
                    </Button>
                </Form>
                 <Table columns={columns} dataSource={this.state.datanum}   pagination={ paginationProps } loading={this.state.loading}  />
          </div>
          </LocaleProvider>
          )
      }
}
const timeorder= Form.create()(timeorderForm);
  export default timeorder;