import React ,{Component} from 'react';
import {Breadcrumb,Form,Button,Select,Input,message } from 'antd'
import axios from 'axios'
import '../../style/projectextend.scss'
import { Link } from 'react-router-dom'
const {Option}=Select



class RegistrationForm extends Component{
      constructor(props){
        super(props)
        this.state={
           chengben:[]
        }
      }
   
    componentDidMount(){
      var loginid=localStorage.getItem('loginid');
      var user=localStorage.getItem('name');
      console.log(loginid,user);
      if(loginid!='' && user!=""){
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        axios.post('/index.php/pro/tui',{
                  name:user,
                  num:loginid
        })
        .then((res)=>{
            console.log(res.data.data);
            console.log(res.data.data.code);
           // data: {code: 402, msg: "登录超时，请重新登录"}
            if(res.data.data.code==402){
              message.info('登录超时，请重新登录');
            }
            this.setState({
              chengben:res.data.data
            })
        })
        .catch((err)=>{
          console.log(err)
        })
      }
    }
    handleSubmit = e => {
      var loginid=localStorage.getItem('loginid');
      var user=localStorage.getItem('name');
      console.log(loginid,user);
      e.preventDefault();
      this.props.form.validateFields((err, values) => {
        if (!err) {
          console.log('提交数据为 ', values);
          if(loginid!='' && user!=""){
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
          axios.post('/index.php/pro/tsou',{
            building_name:values.projectname,
            name:user,
            num:loginid
        })
        .then((res)=>{
            console.log(res.data.data);
            this.setState({
              chengben:res.data.data
            })
        })
        .catch((err)=>{
          console.log(err)
        })

      }
        }
      });
    };
    handleSelectChange=(value)=>{
      localStorage.setItem('dengji',value)
      var loginid=localStorage.getItem('loginid');
      var user=localStorage.getItem('name');
      console.log(value);
      if(value=='一级'){
        var key= localStorage.getItem('dengji');
        console.log(key);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/pro/tuisou',{
            type:key,
            name:user,
           num:loginid
        })
        .then((res)=>{
            console.log(res.data.data);
            this.setState({
              chengben:res.data.data
            })
        })
        .catch((err)=>{
          console.log(err)
        })
      }
          console.log(key)
      }else if(value=='二级'){
        var key= localStorage.getItem('dengji');
        console.log(key);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/pro/tuisou',{
            type:key,
            name:user,
            num:loginid
        })
        .then((res)=>{
            console.log(res.data.data);
            this.setState({
              chengben:res.data.data
            })
        })
        .catch((err)=>{
          console.log(err)
        })
      }
      }else if(value=='三级'){
        var key= localStorage.getItem('dengji');
        console.log(key);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/pro/tuisou',{
            type:key,
            name:user,
            num:loginid
        })
        .then((res)=>{
            console.log(res.data.data);
            this.setState({
              chengben:res.data.data
            })
        })
        .catch((err)=>{
          console.log(err)
        })
      }
      }else if(value=='四级'){
        var key= localStorage.getItem('dengji');
        console.log(key);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/pro/tuisou',{
            type:key,
            name:user,
            num:loginid
        })
        .then((res)=>{
            console.log(res.data.data);
            this.setState({
              chengben:res.data.data
            })
        })
        .catch((err)=>{
          console.log(err)
        })
      }
      }else if(value=='五级'){
        var key= localStorage.getItem('dengji');
        console.log(key);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/pro/tuisou',{
            type:key,
            name:user,
            num:loginid
        })
        .then((res)=>{
            console.log(res.data.data);
            this.setState({
              chengben:res.data.data
            })
        })
        .catch((err)=>{
          console.log(err)
        })
      }
      }
    }
      render(){
        const { getFieldDecorator } = this.props.form;
        return(
          <div className="projectExtend">
                 <Breadcrumb>
                    <Breadcrumb.Item ><Link to="/projectmanage/projectInformation/projectlist"> 项目管理</Link></Breadcrumb.Item>
                    <Breadcrumb.Item ><Link to="/projectmanage/projectextend"> 项目推广等级</Link> </Breadcrumb.Item>
                </Breadcrumb>
                <div className="search">
                    <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
                    <Form.Item label="项目等级">
                      {getFieldDecorator('projectdengji', {
                        rules: [{ required: false, message: '请选择!' }],
                      })(
                        <Select
                        style={{ width:'360px',marginRight:'65px'}}
                          placeholder="一级"
                          onChange={this.handleSelectChange}
                        >
                          <Option value="一级">一级</Option>
                          <Option value="二级">二级</Option>
                          <Option value="三级">三级</Option>
                          <Option value="四级">四级</Option>
                          <Option value="五级">五级</Option>
                        </Select>,
                      )}
                    </Form.Item>  
                      
                    <Form.Item label="项目名称">
                      {getFieldDecorator('projectname', {
                        rules: [{ required: false, message: '请选择!' }],
                      })(
                        <Input   style={{ width:'360px',marginRight:'60px'}}/>
                      )}
                      </Form.Item>
                      <Form.Item>
                        <Button type="primary" htmlType="submit" style={{ width:'88px',marginRight:'210px'}}>查询</Button>
                      </Form.Item>
                    
                    </Form>
                    <br/>
                    
                    <div  className="chengbenbox">
                        <p className="title">不计成本项目</p>
                        <br/>

                        <ul className="chengbenlist">
                        {
                   this.state.chengben.length>0?this.state.chengben.map((item,index)=>{
                               return(
                                 <li key={index}>{item}</li>
                               )
                           }):'暂无数据'
                        }
                         </ul>
                    </div>
                    </div>
          </div>
          )
      }
}
const projectExtend = Form.create()(RegistrationForm);
  export default projectExtend